* {
    margin: 0;
    padding: 0;
}
.travel_wrap {
    width: 100%;
    height: auto;
    background: #fff;

    .BG {
        width: 100%;
        height: 600px;
        position: relative;
        .header_wrap {
            position: absolute;
            top: 20px;
            left: 20px;
            width: calc(100% - 40px);
            height: 60px;
            display: flex;
            // background: #000;
            button {
            background: inherit; 
            border:none; 
            box-shadow:none; 
            border-radius:0; 
            padding:0; 
            overflow:visible; 
            cursor:pointer
            }
            .Home {
                height: auto;
                position: absolute;
                left: 0;
            }
            .login_but {
                font-weight: 900;
                .Login {
                    position: absolute;
                    right: 60px;
                }
                .Sing {
                    position: absolute;
                    right: 0px;
                }
            }
        }
        .bottom_wrap {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -40%);
            width: calc(100% - 380px);
            height: auto;
            // background: #acacac;
            h3 {
                text-align: center;
                font-size: 60px;
                margin-bottom: 60px;
            }
            .search_wrap {
                
                input {
                    width: 100%;
                    height: 60px;
                    border-radius: 100px;
                    border: 2px solid #000;
                    background: none;
                    -webkit-appearance: none;
                    -moz-appearance: none;
                    appearance: none;
                    outline:none;
                    padding-left: 30px;
                    font-size: 20px;
                    
                }
                input::placeholder {
                    font-size: 15px;
                    color: #fff;
                }
            }
            h6 {
                text-align: center;
                font-size: 20px;
                margin-top: 60px;
            }
        }
    }
    .image-container {
        position: relative;
        width: 100%;
    }
    .image-container img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: auto;
        object-fit: cover;
        transition: opacity 1s ease-in-out;
        opacity: 0;
    }
    .image-container img.visible {
        opacity: 1;
    }
    .Trv_main {
        width: 100%;
        height: 2000px;
        background: #fff;
        .Trv_container {
            width: calc(100% - 80px);
            height: 1800px;
            margin: 0 auto;
            // border: 1px solid #e4e4e4;

            margin-top: 50px;
            position: relative;
            .try_title {
                width: 100%;
                height: 60px;
                position: absolute;
                top: 20px;
                border-bottom: 1px solid #d9d9d9;
                h4 {}
                .navbar {
                    position: absolute;
                    right: 0;
                }
                .navbar button {
                    background-color: #f8f9fa;
                    border: none;
                    border-bottom: none;
                    border-radius: 4px;
                    padding: 10px 20px;
                    margin: 0 5px;
                    cursor: pointer;
                    transition: background-color 0.3s;
                }
                .navbar button:hover {
                    background-color: #e2e6ea;
                }
                .navbar button.active {
                    background-color: #007bff;
                    color: white;
                    // border-color: #007bff;
                }
            }
            .content {
                border-radius: 4px;
                background-color: #ffffff;
                width: 100%;
                height: 500px;
                // background: #bfbfbf;
                margin-top: 80px;
            }
        }
    }
}
