.medicine-page {
    .title_wrap {
      width: 100%;
      height: auto;
      .container {
        width: calc(100% - 140px);
        height: auto;
        margin: 0 auto;
        .theme_title {
          .choice {
            display: flex;
            padding-top: 10px;
            margin-bottom: 10px;
            margin-left: -10px;
          }
          .choice button {
            background-color: #f0f0f0;
            border: 2px solid transparent;
            border-radius: 5px;
            padding: 10px 20px;
            margin: 0 10px;
            cursor: pointer;
            transition: all 0.3s ease;
          }
          .choice button:hover {
            background-color: #e0e0e0;
          }
          .choice button.active {
            background-color: #1472C7;
            border-color: #0f5a9d;
            color: white;
            border: none;
          }
        }
        .box_wrap {
          display: flex;
          flex-wrap: wrap;
          .location_wrap {
            margin-right: 35px;
            width: calc(30.3333% - 20px);
            height: 330px;
            border: 1px solid #d9d9d9;
            border-radius: 5px;
            padding: 10px;
            margin-bottom: 20px;
            :last-child {
              margin-right: none !important;
            }
            .location {
              margin-left: 20px;
              margin-bottom: 10px;
              display: flex;
              .tre_title {
                font-size: 18px;
                cursor: pointer;
                color: #1472C7;
                font-weight: 800;
                margin-right: 20px;
              }
              .tre_location {
                font-size: 14px;
                color: #676767;
              }
            }
            .tre_desc {
              margin-left: 20px;
              margin-bottom: 10px;
              font-size: 14px;
              height: 80px;
            }
            .tre_hash {
              margin-left: 20px;
              margin-bottom: 10px;
              width: auto;
              height: auto;
              display: flex;
              text-align: center;
              align-items: center;
              white-space: nowrap;
              h6 {
                padding: 5px;
                margin-right: 8px;
                width: auto;
                height: auto;
                background: #F0F0F3;
                border-radius: 5px;
                color: #555558;
              }
            }
            .tre_img {
              display: flex;
              justify-content: center;
              align-items: center;
              margin-left: auto;
              margin-right: auto;
              width: 70%;
              height: 70%;
              img {
                max-width: 100%;
                max-height: 100%;
                width: auto;
                height: auto;
                border-radius: 10px;
              }
            }
            .booking {
              margin-left: 20px;
              width: 355px;
              height: 40px;
              border: 1px solid #e4e4e4;
              border-radius: 5px;
              text-align: center;
              justify-content: center;
              display: flex;
              margin-top: 10px;
              h5 {
                margin-top: 10px;
                cursor: pointer;
              }
            }
          }
        }
      }
    }
  }
  